import api from './api'
import { User, UserJP } from '@models'

const END_POINT = 'https://jsonplaceholder.typicode.com'

export const getUsers = async (): Promise<User[]> => {
  const response: User[] = await api.get(`${END_POINT}/users`)
  return response.map((user: User) => ({
    ...user,
    avatar: `https://avatars.dicebear.com/v2/avataaars/${user.id}.svg`
  }))
}

// LEAK: leaking data from return unnecessarily props
export const getUsersJP = async (): Promise<User[]> => {
  const res =
    (await api.get(`/users.json`, {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      }
    })) || []
  return res.map((user: UserJP) => ({
    id: user.id,
    avatar: `https://avatars.dicebear.com/v2/avataaars/${user.id}.svg`,
    name: user.name,
    email: user.email,
    phone: user.phone
  }))
}
